@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);

body {
  margin: 0;
  font-family: 'Roboto';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::after,
*::before{
  padding: 0;
  margin: 0;
}

.container{
  height: 100vh;
  width: 100%;
  display: flex;

  font-family: 'Roboto';
}

.container .section{
  flex: 1 1;
  height: 100%;
  position: relative;
  padding: 0px 5px;
  overflow: hidden;
}

.MuiAppBar-root{
  box-shadow: none;
}
